<style lang="scss" scope>
.ui-page{
    padding-bottom: 20px;
    .ui-search{
        margin-bottom: 20px;
        .ui-search-header{
            text-align: left;
        }
    }
}
.ui-box{
    border-radius: 8px;
    background-color: #FFF;
}

</style>
<template>
    <div class="ui-page">
        <div class="ui-search ui-box">
            <div class="box-header ui-search-header">条件查询</div>
            <slot name="search-form"></slot>
        </div>
            <slot name="list-box"></slot>
    </div>
</template>
<script>
export default {
    name: 'pageBox',
}
</script>





